<template>
  <div class="text-float">
    <div class="wordbox-para gradient-text-two">
      <div :key="index" v-for="(itemArr, index) in wordArr">
        <span
          :style="{ 'animation-delay': item.time + 's' }"
          :key="index"
          v-for="(item, index) in itemArr"
          >{{ item.word }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      wordList: [
        '入夜渐微凉 繁花落地成霜',
        '你在远方眺望 耗尽所有暮光',
        '不思量 自难相忘 夭夭桃花凉',
        '前世你怎舍下 这一海心茫茫',
        '还故作不痛不痒不牵强 都是假象',
        '凉凉夜色为你思念成河',
        '化作春泥呵护着我',
        '浅浅岁月拂满爱人袖',
        '片片芳菲入水流',
        '凉凉天意潋滟一身花色',
        '落入凡尘伤情着我',
        '生劫易渡情劫难了',
        '折旧的心 还有几分前生的恨',
        '还有几分 前生的恨',
        '也曾鬓微霜 也曾因你回光',
        '悠悠岁月漫长 怎能浪费时光',
        '去流浪 去换成长 灼灼桃花凉',
        '前世你怎舍下 一朵已放心上',
        '足够三生三世背影成双 在水一方'
      ],
      time: 0,
      speed: 0.4
    }
  },
  computed: {
    wordArr: function () {
      let arr = []

      let index = 0

      for (let i = 0; i < this.wordList.length; i++) {
        let item = this.wordList[i]
        let dataArr = []
        let itemArr = item.split('')
        for (let j = 0; j < itemArr.length; j++) {
          index += this.speed
          let obj = { word: itemArr[j], time: index }
          dataArr.push(obj)
        }
        console.log(itemArr)
        arr.push(dataArr)
      }

      return arr
    }
  },
  mounted () {
    console.log(this.wordArr)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.text-float {
  position: absolute;
  left: 2rem;
  font-size: 0.9rem;
  margin: auto;
  padding-top: 2rem;
  z-index: 2000;
  line-height: 1.4;
  color: #fff;
  font-family: "\6977\4F53";
}

.wordbox-para span {
  display: inline-block;
  -webkit-animation: landIn 0.8s ease-out both;
  animation: landIn 0.8s ease-out both;
}
@keyframes landIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-30%);
    transform: translateY(-30%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(1%);
    transform: translateY(1%);
  }
}
</style>
